import React, { useContext } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';
import NavbarWrapper from './wrapper';
import Drawer from '../Drawer';
import Button from '../Button';
import Logo from '../Logo/index';
import Box from '../Box';
import HamburgMenu from '../HamburgMenu';
import Container from '../Container/index';
import { DrawerContext } from '../../../context/drawerContext';
import ScrollSpyMenu from '../ScrollSpyMenu/index';
import { Link } from 'gatsby';

// import LogoImage from '../../../images/logo-white.png';
// import LogoImageAlt from '../../../images/logo.png';
import LogoImage from '../../../images/logo.jpeg';
import { APP_ROUTES } from '../../../constants';

const Navbar = ({ navbarStyle, logoStyle, button, row, menuWrapper }) => {
    const Data = useStaticQuery(graphql`
        query {
            dataJson {
                MENU_ITEMS {
                    label
                    path
                    offset
                }
            }
        }
    `);

    const { state, dispatch } = useContext(DrawerContext);

    // Toggle drawer
    const toggleHandler = () => {
        dispatch({
            type: 'TOGGLE',
        });
    };

    return (
        <NavbarWrapper {...navbarStyle} className="saas_navbar">
            <Container>
                <Box {...row}>
                    <Logo
                        href="/saasmodren"
                        logoSrc={LogoImage}
                        title="Portfolio"
                        logoStyle={logoStyle}
                        className="main-logo"
                    />
                    <Logo
                        href="/saasmodren"
                        logoSrc={LogoImage}
                        title="Portfolio"
                        logoStyle={logoStyle}
                        className="logo-alt"
                    />
                    <Box {...menuWrapper}>
                        <ScrollSpyMenu
                            className="main_menu"
                            menuItems={Data.dataJson.MENU_ITEMS}
                            offset={-70}
                        />
                        <Link to={APP_ROUTES.LOGIN} className="navbar_button">
                            <Button {...button} title="GET STARTED" />
                        </Link>
                        <Drawer
                            width="420px"
                            placement="right"
                            drawerHandler={<HamburgMenu barColor="#fff" />}
                            open={state.isOpen}
                            toggleHandler={toggleHandler}
                        >
                            <ScrollSpyMenu
                                className="mobile_menu"
                                menuItems={Data.dataJson.MENU_ITEMS}
                                drawerClose={true}
                                offset={-100}
                            />
                            <Link to={APP_ROUTES.LOGIN} className="navbar_drawer_button">
                                <Button {...button} title="GET STARTED" />
                            </Link>
                        </Drawer>
                    </Box>
                </Box>
            </Container>
        </NavbarWrapper>
    );
};

Navbar.propTypes = {
    navbarStyle: PropTypes.object,
    logoStyle: PropTypes.object,
    button: PropTypes.object,
    row: PropTypes.object,
    menuWrapper: PropTypes.object,
};

Navbar.defaultProps = {
    navbarStyle: {
        minHeight: '70px',
        display: 'block',
    },
    row: {
        flexBox: true,
        justifyContent: 'space-between',
        alignItems: 'center',
        width: '100%',
    },
    logoStyle: {
        maxWidth: ['120px', '130px'],
    },
    button: {
        type: 'button',
        fontSize: '13px',
        fontWeight: '700',
        borderRadius: '4px',
        pl: '15px',
        pr: '15px',
        colors: 'secondaryWithBg',
        minHeight: 'auto',
        height: '40px',
    },
    menuWrapper: {
        flexBox: true,
        alignItems: 'center',
    },
};

export default Navbar;
